import {Component, OnInit, ViewEncapsulation} from '@angular/core';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {PageTitleService} from '../../../core/page-title/page-title.service';
import {fadeInAnimation} from "../../../core/route-animation/route.animation";
import {Profil} from "../metier/profil.metier";
import {ProfilService} from "../service/profil.service";
import {BaseComponent} from "../../../core/component/base-component";
import {ActivatedRoute, Params} from "@angular/router";

@Component({
  selector: 'ms-profil-edit',
  templateUrl: './profil.edit.component.html',
  styleUrls: ['./profil.edit.component.scss'],
  encapsulation: ViewEncapsulation.None,
  host: {
    "[@fadeInAnimation]": 'true'
  },
  animations: [fadeInAnimation]
})
export class ProfilEditComponent extends BaseComponent implements OnInit {
  private profil: Profil;
  public profilForm: FormGroup;


  constructor(private fb: FormBuilder,
              private route: ActivatedRoute,
              private pageTitleService: PageTitleService,
              private profilService: ProfilService) {
    super();
  }

  ngOnInit() {
    this.loadProfil();
    this.pageTitleService.setTitle("MODIFIER UN PROFIL");
    this.profilForm = this.fb.group({
      id: [Validators.required],
      libelle: ['', Validators.required],

    });
  }


  updateProfil(profilForm: any) {
    this.profil = new Profil(profilForm);
    this.profilService.saveProfil(this.profil)
      .subscribe(() => {
        this.showInfo("Enregistrement effectué avec succès");
        this.router.navigate(['/admin/profil/list']); // Redierction vers list
      }, error => this.showError(error.status, error.json().message))
    ;
  }

  loadProfil() {
    this.route.params.switchMap((params: Params) => this.profilService.getProfil(+params['id']))
      .subscribe(profil => {
          this.profil = profil;
          this.profilForm.patchValue(this.profil);
        }, error => this.showError(error.status, error.json().message)
      );
  }

  deleteProfil(profilForm: any) {
    if (!confirm("Etes-vous de vouloir supprimer ces enregistrements !")) {
      return;
    }
    this.profil = new Profil(profilForm);
    this.profilService.deleteProfil(this.profil)
      .subscribe(() => {
        this.showInfo("Suppression effectué avec succès");
        this.router.navigate(['/admin/profil/list']); // Redierction vers list
      }, error => this.showError(error.status, error.json().message))
    ;
  }

  profilRoleRedirect() {
    this.router.navigate(['/admin/profilRole/list'], {queryParams: {profil_id: this.profil.id}});
  }

}






