import {NgModule} from '@angular/core';
import {RouterModule} from '@angular/router';
import {CommonModule} from '@angular/common';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import {FileUploadModule} from 'ng2-file-upload/ng2-file-upload';
import {WizardModule} from 'ng2-archwizard';
import {ProfilAddComponent}  from './profil.add/profil.add.component';
import {ProfilRoutes} from './profil.routing';
import {ProfilService} from "./service/profil.service";
import {ProfilListComponent} from "./profil.list/profil.list.component";
import {ProfilEditComponent} from "./profil.edit/profil.edit.component";
import {DataTableModule} from "angular2-datatable";

@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    ReactiveFormsModule,
    FileUploadModule,
    DataTableModule,
    WizardModule,
    RouterModule.forChild(ProfilRoutes)
  ],
  declarations: [
    ProfilAddComponent,
    ProfilListComponent,
    ProfilEditComponent,
  ],
  providers:[
    ProfilService,
  ],
})

export class ProfilModule {
}



