import {Component, OnInit, ViewEncapsulation} from '@angular/core';
import {FormBuilder, FormGroup, Validators, FormControl} from '@angular/forms';
import {CustomValidators} from 'ng2-validation';
import {PageTitleService} from '../../../core/page-title/page-title.service';
import {fadeInAnimation} from "../../../core/route-animation/route.animation";
import {Profil} from "../metier/profil.metier";
import {ProfilService} from "../service/profil.service";
import {BaseComponent} from "../../../core/component/base-component";

const password = new FormControl('', Validators.required);
const confirmPassword = new FormControl('', CustomValidators.equalTo(password));

@Component({
  selector: 'ms-profil-add',
  templateUrl: './profil.add.component.html',
  styleUrls: ['./profil.add.component.scss'],
  encapsulation: ViewEncapsulation.None,
  host: {
    "[@fadeInAnimation]": 'true'
  },
  animations: [fadeInAnimation]
})
export class ProfilAddComponent extends BaseComponent implements OnInit {

  public profilForm:FormGroup;
  private profil:Profil;

  constructor(private fb:FormBuilder, private pageTitleService:PageTitleService, private profilService: ProfilService) {
    super();
  }

  ngOnInit() {
    this.pageTitleService.setTitle("AJOUTER UN PROFIL");
    this.profilForm = this.fb.group({
      libelle: ['', Validators.required],
    });
  }

  saveProfil(profilForm: any) {
    this.profil = new Profil(profilForm);
    this.profilService.saveProfil(this.profil)
      .subscribe(() => {
        this.showInfo("Enregistrement effectué avec succès");
        this.reset();
        this.router.navigate(['/admin/profil/list']); // Redierction vers list
      }, error => this.showError(error.status, error.json().message))
    ;
  }

  reset() {
    this.profilForm.reset();
  }


}






