"use strict";

import { Component, ViewChild, OnInit, ViewEncapsulation } from '@angular/core';
import { PageTitleService } from '../../../core/page-title/page-title.service';
import { fadeInAnimation } from "../../../core/route-animation/route.animation";
import { Profil } from "../metier/profil.metier";
import { FormGroup, FormBuilder } from "@angular/forms";
import { BaseComponent } from "../../../core/component/base-component";
import { ProfilService } from "../service/profil.service";
import 'rxjs/Rx';
import 'rxjs/add/operator/toPromise';

@Component({
  selector: 'ms-profil-list',
  templateUrl: './profil.list.component.html',
  styleUrls: ['./profil.list.component.scss'],
  encapsulation: ViewEncapsulation.None,
  host: {
    "[@fadeInAnimation]": 'true'
  },
  animations: [fadeInAnimation]

})
export class ProfilListComponent extends BaseComponent implements OnInit {

  public profilForm: FormGroup;
  private profil: Profil;
  private profilList: Array<Profil> = [];
  private file: Blob;

  constructor(private fb: FormBuilder,
    private profilService: ProfilService,
    private pageTitleService: PageTitleService) {
    super();
    this.createForm();
  }

  ngOnInit() {
    this.pageTitleService.setTitle("LISTE DES PROFILS");
    this.loadProfilList();

  }

  createForm() {
    this.profilForm = this.fb.group({
      libelle: '',
    });
  }

  private loadProfilList() {

    this.profilService.getProfilList()
      .subscribe(profilList => {
        this.profilList = profilList;
      }, error => this.showError(error.status, error.json().message))
      ;
  }

}






